import { createGlobalState } from "react-hooks-global-state";

// variavis de estado global que monitoram a pgina atual bem como o estado de autenticao para a criao de uma sesso de usurio.
const { setGlobalState, useGlobalState } = createGlobalState({
  currentPage: 'login',
  isAuthenticated: false,
});

export const setCurrentPage = (page) => {
  setGlobalState('currentPage', page);
};

export const setAuthenticated = (state) => {
  setGlobalState('isAuthenticated', state);
};

export { useGlobalState };